/* ----------------------------------------------------------------------------- # Custom Implementation of Normalize.css and Sanitize.css ----------------------------------------------------------------------------- */
:root { box-sizing: border-box; font-size: 16px; font-family: sans-serif; line-height: 1.6; -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

@-ms-viewport { width: device-width; }

html { -ms-overflow-style: scrollbar; -webkit-tap-highlight-color: transparent; }

*, *::before, *::after { box-sizing: inherit; border: none; margin: 0; padding: 0; background-repeat: no-repeat; }

::before, ::after { text-decoration: inherit; vertical-align: inherit; }

body { min-width: 100%; min-height: 100vh; font-size: 100%; background-color: white; overflow-x: hidden; overflow-y: scroll; -webkit-overflow-scrolling: touch; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; text-rendering: optimizeLegibility; }

article, aside, footer, header, nav, section, details, figcaption, figure, main, menu, summary { display: block; }

h1 { font-size: 2em; }

figure { margin: 1em 40px; }

hr { box-sizing: content-box; height: 0; overflow: visible; }

pre { font-family: monospace, monospace; font-size: 1em; }

ol, ul { padding-left: 1.25rem; }

nav ol, nav ul { list-style: none; }

a { background-color: transparent; -webkit-text-decoration-skip: objects; }

a:active, a:hover { outline-width: 0; }

abbr[title] { border-bottom: 1px dotted; text-decoration: none; }

b, strong { font-weight: 800; }

code, kbd, samp { font-family: monospace, monospace; font-size: 1em; }

dfn { font-style: italic; }

mark { background-color: yellow; color: black; }

small { font-size: 80%; }

sub, sup { font-size: 75%; line-height: 0; position: relative; vertical-align: baseline; }

sub { bottom: -0.25em; }

sup { top: -0.5em; }

::-moz-selection { background-color: #b3d4fc; color: black; text-shadow: none; }

::selection { background-color: #b3d4fc; color: black; text-shadow: none; }

audio, canvas, iframe, img, svg, video, progress { display: inline-block; vertical-align: middle; }

audio:not([controls]) { display: none; height: 0; }

img { border-style: none; }

svg:not(:root) { overflow: hidden; }

button, input, optgroup, select, textarea { font-family: sans-serif; font-size: 100%; line-height: 1.6; }

button, input { overflow: visible; }

button, select { text-transform: none; }

button, html [type="button"], [type="reset"], [type="submit"] { -webkit-appearance: button; }

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner { border-style: none; }

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring { outline: 1px dotted ButtonText; }

fieldset { border: 1px solid silver; margin: 0 2px; padding: 0.35em 0.625em 0.75em; }

legend { color: inherit; display: table; max-width: 100%; white-space: normal; }

progress { vertical-align: baseline; }

textarea { overflow: auto; resize: vertical; }

::-webkit-inner-spin-button, ::-webkit-outer-spin-button { height: auto; }

[type="search"] { -webkit-appearance: textfield; outline-offset: -2px; }

::-webkit-search-cancel-button, ::-webkit-search-decoration { -webkit-appearance: none; }

::-webkit-file-upload-button { -webkit-appearance: button; font: inherit; }

::-webkit-input-placeholder { color: inherit; opacity: 0.54; }

[aria-busy="true"] { cursor: progress; }

[aria-controls] { cursor: pointer; }

[aria-disabled] { cursor: default; }

[hidden][aria-hidden="false"] { clip: rect(0, 0, 0, 0); display: inherit; position: absolute; }

[hidden][aria-hidden="false"]:focus { clip: auto; }

a, area, button, input, label, select, textarea, [tabindex] { -ms-touch-action: manipulation; touch-action: manipulation; }

details, menu { display: block; }

summary { display: list-item; }

template, [hidden] { display: none; }

/* ----------------------------------------------------------------------------- # Grid System ----------------------------------------------------------------------------- */
.grid-system { display: flex; flex-wrap: wrap; }

.grid-system > * { padding-left: 0 0.5rem; padding-right: 0 0.5rem; }

.grid-system > div { flex: 1 1 0%; width: auto; }

.grid-system > div:nth-child(1) { flex: 0 0 25%; min-width: 25%; }

@media screen and (min-width: 600px) { .grid-system > div:nth-child(1) { flex: 0 0 58.33333%; min-width: 58.33333%; } }

@media screen and (min-width: 900px) { .grid-system > div:nth-child(1) { flex: 0 0 16.66667%; min-width: 16.66667%; } }

.grid-system > div:nth-child(2) { flex: 0 0 8.33333%; min-width: 8.33333%; }

@media screen and (min-width: 600px) { .grid-system > div:nth-child(2) { flex: 0 0 16.66667%; min-width: 16.66667%; } }

@media screen and (min-width: 900px) { .grid-system > div:nth-child(2) { flex: 0 0 25%; min-width: 25%; } }

.grid-system > div:nth-child(3) { flex: 0 0 50%; min-width: 50%; }

@media screen and (min-width: 600px) { .grid-system > div:nth-child(3) { flex: 0 0 8.33333%; min-width: 8.33333%; } }

@media screen and (min-width: 900px) { .grid-system > div:nth-child(3) { flex: 0 0 41.66667%; min-width: 41.66667%; } }

.grid-system > div:nth-child(4) { flex: 0 0 16.66667%; min-width: 16.66667%; }

@media screen and (min-width: 600px) { .grid-system > div:nth-child(4) { flex: 0 0 16.66667%; min-width: 16.66667%; } }

@media screen and (min-width: 900px) { .grid-system > div:nth-child(4) { flex: 0 0 16.66667%; min-width: 16.66667%; } }

/* ----------------------------------------------------------------------------- # Images ----------------------------------------------------------------------------- */
.image { display: block; position: relative; }

.image img { display: block; width: 100%; height: auto; }

.image.is-square img, .image.is-1by1 img, .image.is-4by3 img, .image.is-3by2 img, .image.is-16by9 img, .image.is-2by1 img { position: absolute; top: 0; right: 0; bottom: 0; left: 0; width: 100%; height: 100%; }

.image.is-square, .image.is-1by1 { padding-top: 100%; }

.image.is-4by3 { padding-top: 75%; }

.image.is-3by2 { padding-top: 66.6666%; }

.image.is-16by9 { padding-top: 56.25%; }

.image.is-2by1 { padding-top: 50%; }

.image.is-16x16 { width: 16px; height: 16px; }

.image.is-24x24 { width: 24px; height: 24px; }

.image.is-32x32 { width: 32px; height: 32px; }

.image.is-48x48 { width: 48px; height: 48px; }

.image.is-64x64 { width: 64px; height: 64px; }

.image.is-96x96 { width: 96px; height: 96px; }

.image.is-128x128 { width: 128px; height: 128px; }

/* ----------------------------------------------------------------------------- # Typography ----------------------------------------------------------------------------- */
.content h1, .content h2, .content h3, .content h4, .content h5, .content h6 { margin-bottom: 20px; }

.content blockquote:not(:last-child), .content p:not(:last-child), .content ol:not(:last-child), .content ul:not(:last-child) { margin-bottom: 1em; }

/* ----------------------------------------------------------------------------- # Utility Classes ----------------------------------------------------------------------------- */
.round { border-radius: 0.3em; }

.block { display: block; width: 100%; }

.empty { margin: 20px 0; height: 1px; }

img.responsive, video.responsive, iframe.responsive, embed.responsive, object.responsive { max-width: 100%; height: auto; }

.embed { position: relative; padding-bottom: 56.25%; height: 0; overflow: hidden; }

.embed.is-4by3 { padding-bottom: 75%; }

.embed iframe, .embed object, .embed embed, .embed video { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.section { padding: 40px 0; }

.section + .section { padding-top: 0; }

.container { position: relative; width: 90%; max-width: 75em; margin-left: auto; margin-right: auto; }

.off-screen-top { transform: translateY(-100vh); }

.off-screen-bottom { transform: translateY(100vh); }

.off-screen-left { transform: translateX(-100vh); }

.off-screen-right { transform: translateX(100vh); }

.is-open { display: block; }

.is-closed { display: none; }

/* ----------------------------------------------------------------------------- # Accordion ----------------------------------------------------------------------------- */
.accordion { max-width: 600px; margin: 2rem auto; width: 90%; background: #f4f4f4; }

.accordion_panel-header { padding: 1rem 1.5rem; background: #000; color: #fff; cursor: pointer; transition: 0.3s ease; }

.accordion_panel-header:hover { background: #404040; }

.accordion_panel-content { display: none; padding: 1rem 1.5rem; color: #262626; }

/* ----------------------------------------------------------------------------- # Buttons ----------------------------------------------------------------------------- */
button, .button { -webkit-appearance: none; border: 0.1rem solid #000; color: #000; cursor: pointer; display: inline-block; font-size: 1rem; line-height: 1.6rem; outline: 0; padding: 0.5em 1.5em; text-align: center; text-decoration: none; user-select: none; vertical-align: middle; white-space: nowrap; }

button:focus, .button:focus { background: #030303; }

button:hover, .button:hover { background: #000; border-color: black; color: #fff; }

button:active, button.active, .button:active, .button.active { background: black; border-color: black; color: #fff; }

button.primary, .button.primary { background: #1c7293; }

/* ----------------------------------------------------------------------------- # Call to Action ----------------------------------------------------------------------------- */
.call-to-action { width: 100%; min-height: 30vmin; padding: 1.5em 1em; font-size: 1rem; display: flex; flex-direction: column; justify-content: center; align-items: center; }

@media screen and (min-width: 37.5em) { .call-to-action { font-size: 1.5rem; } }

.call-to-action_title { font-size: 1.5rem; }

@media screen and (min-width: 37.5em) and (max-width: 61.25em) { .call-to-action_title { font-size: 1.85rem; } }

@media screen and (min-width: 61.3125em) { .call-to-action_title { font-size: 2.5rem; } }

/* ----------------------------------------------------------------------------- # Card(s) ----------------------------------------------------------------------------- */
.card { position: relative; border-radius: 2px; background-color: #fff; border: 1px solid black; max-width: 300px; }

.card_image { position: relative; }

.card_image img { display: block; border-radius: 2px 2px 0 0; position: relative; left: 0; right: 0; top: 0; bottom: 0; width: 100%; }

.card_image .card_title { color: #fff; position: absolute; bottom: 0; left: 0; padding: 20px; }

.card_content { padding: 20px; }

.card_content p { margin: 0; }

.card_title { font-size: 1.5rem; line-height: 2em; }

.card_actions { position: relative; background-color: inherit; border-top: 1px solid rgba(0, 0, 0, 0.2); padding: 20px; }

.card_actions a { font-size: 0.875em; color: #ffab40; margin-right: 20px; transition: color 0.3s ease; text-transform: uppercase; }

/* ----------------------------------------------------------------------------- # Full Screen Video ----------------------------------------------------------------------------- */
.fullscreen-video { display: none; position: fixed; top: 50%; left: 50%; min-width: 100%; width: auto; min-height: 100%; height: auto; z-index: -100; transform: translateX(-50%) translateY(-50%); background: transparent url(../video/sample-poster.jpg) center center/cover no-repeat; }

@media screen and (min-width: 61.3125em) { .fullscreen-video { display: block; } }

/* ----------------------------------------------------------------------------- # Hero ----------------------------------------------------------------------------- */
.hero { width: 100%; min-height: 40vmax; max-height: 500px; padding: 1.5em 1em; font-size: 2rem; display: flex; flex-direction: column; justify-content: center; }

@media screen and (min-width: 37.5em) and (max-width: 61.25em) { .hero { font-size: 3rem; } }

@media screen and (min-width: 61.3125em) { .hero { min-height: 35vmax; font-size: 4rem; } }

.hero_title { font-size: inherit; }

/* ----------------------------------------------------------------------------- # Media Item ----------------------------------------------------------------------------- */
.media-item { display: flex; flex-direction: column; align-items: flex-start; }

@media screen and (min-width: 37.5em) { .media-item { flex-direction: initial; } }

.media-item_image { flex: 100%; }

@media screen and (min-width: 37.5em) { .media-item_image { flex: none; margin-right: 1em; } }

.media-item_content { flex: 100%; order: 1; }

@media screen and (min-width: 37.5em) { .media-item_content { flex: 1; } }

/* ----------------------------------------------------------------------------- # Social Links ----------------------------------------------------------------------------- */
.social-links ul { list-style: none; margin: 0; padding: 0; display: flex; }

.social-links li:not(:last-child) a { margin-right: 0.625rem; }

.social-links a { display: inline-block; font-size: 1px; color: transparent; width: 50px; height: 50px; }

.social-links a::before { content: ""; position: absolute; width: 50px; height: 50px; background-size: contain; background-position: center center; background-repeat: no-repeat; }

a[href*="facebook.com"]::before { background-image: url(../images/facebook.svg); }

a[href*="instagram.com"]::before { background-image: url(../images/instagram.svg); }

a[href*="github.com"]::before { background-image: url(../images/github.svg); }

/* ----------------------------------------------------------------------------- # Fifty-Fifty Split ----------------------------------------------------------------------------- */
.fifty-fifty { width: 100%; display: flex; flex-direction: column; }

@media screen and (min-width: 61.3125em) { .fifty-fifty { flex-direction: initial; } }

.fifty-fifty > div { width: 100%; flex: none; }

@media screen and (min-width: 61.3125em) { .fifty-fifty > div { width: 50%; } }

/* ----------------------------------------------------------------------------- # Grid - Not to be confused with a grid system, but an actual grid ----------------------------------------------------------------------------- */
.grid { display: flex; flex-wrap: wrap; justify-content: center; }

.grid > div { flex-grow: 1; }

.grid-1 > div { flex-grow: initial; width: 100%; }

.grid-2 > div { flex-grow: initial; width: calc(100% / 2); }

.grid-3 > div { flex-grow: initial; width: calc(100% / 3); }
